import React, { Component } from 'react';
import Recomendados from './Recomendados';
import Palabras from './Palabras';
import Tops from './Tops';
import Ultimos from './Ultimos';
import './Inicio.css';
import MenuPrincipal from './MenuPrincipal';

class Inicio extends Component {

    constructor(props) {
        super(props)
        this.state = {

        }
    }

    render() {
        return (<div className="debajo-barra">
            <MenuPrincipal />
            <div className="contenedor-principal">
                <Recomendados
                tarjetas={[
                    {titulo:'Mila Luna', cuerpo:'Cuando era joven, solía pasar las vacaciones de verano mochilenado  por el norte. Recorriendo el gran desierto por las noches y disfrutando del paisaje como solo un viajero solitario puede. Mi vieja siempre me aconsejaba que confiase más en los ancianos, que no bebiera donde los caballos no lo hacían y obedeciera, sin discusiones, las instrucciones del dueño de casa.'},
                    {titulo:'Legend Stories (Season 1)', cuerpo:'Una organización de humanos con poderes sobrenaturales llamada "Legends" se tienen que enfrentar a sus peores enemigos llamados los "etherianos" que aterrorizan a RunaTerra, un mundo lleno de magia y misterio. Los miembros de la organización Legends mas importantes son Ahri, Akali, Evelynn y Kaisa entre otros que se verán en sus caminos. Ellas sueñan con vengarse de los etherianos juntas, pero el destino no será muy fácil para todas, puede que su destino sea la muerte o puede que no... A veces las cosas no salen como nosotros pensamos. ¿Que harán para enfrentarse al ejercito mas fuerte RunaTerra?'},
                    {titulo:'Repárame',cuerpo:'Jungkook sufrió mucho durante su infancia; padecer obesidad y ser hijo de una mujer bipolar que se niega a tomar su medicamento y un hombre al que sólo le importa la herencia de su esposa no fue nada fácil. Ahora, que tuvo que mudarse a Seúl a sus 17 años por una orden de restricción en contra de su madre, sólo desea que el esfuerzo que ha invertido en mantenerse medianamente saludable rinda frutos. Y al parecer su recompensa vendrá con la apariencia de un lindo chico de cabello gris y sonrisa contagiosa.'},
                    {titulo:'El primer amor nunca se olvida', cuerpo:'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industrys standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.'},
                    {titulo:'Destinos Cruzados', cuerpo:'Ella es una chica linda y tímida sin muchos amigos, apesar de que es timida si se meten con sus amigos se meten con ella ya que los protege como si fueran parte de su familia, tiene un caracter fuerte pero no lo demuestra si no es necesario, aficionada de los deportes, la lectura, el cine, los videojuegos e ir a un grupo en la Iglesia con sus amigos. Ella nunca se ha enamorado por así decirlo aunque a ella no le llama la atención ya que quiere acabar los estudios con un buen promedio. Lo que ella no sabe es que el destino le tiene pensado otra cosa para ella.'}
                ]}
                />
                <Ultimos 
                tarjetas={[
                    {titulo:'Araceli', cuerpo:'Yo no sé qué sucedió, Araceli. Quizás te disgustaste por algo que nunca me enteré. A pesar de conocerte tan poco tiempo te había llegado a apreciar realmente. A veces, te extraño. Pienso en ti, quisiera que todo vuelva a ser como antes, cuando nos prometíamos tantas cosas que podían hacerse realidad. Quizás tu orgullo fue mayor o te dijeron algo que cambió radicalmente tu opinión.'},
                    {titulo:'Fucking feelings', cuerpo:'Tú mirada siempre hace que me ilusione, con tú mirada me siento  protegida, con tú mirada me enamoras, Tú mirada es todo para mí. (Piccolo Scrittore)'},
                    {titulo:'¿Dónde está el perro?', cuerpo:'-Yo creo que los labios son las llaves de su templo. Besar sin amar es lo más mezquino. Pero el perro me miró estupefacto. Noté como una sonrisa se empezó a dibujar en su rostro alargado. Ante los ojos del perro, nada de eso era malo, sino risible. Hace mucho frío esta mañana. En la facultad de psicología veo a conocidos y desconocidos pasar.'}
                ]}
                />
                <Tops
                tarjetas={[
                    {titulo:'El pez', cuerpo:'Era una tarde cualquiera, una tarde dentro de una época en la que todo parecía ser más simple. Sí, sin duda una tarde inolvidable en la que el sol brillaba en su máximo esplendor y normalmente nada pasaba de lo normal a lo extraordinario. Nada -me aventuré a pensar- excepto por lo que ocurrió ese mismo día.'},
                    {titulo:'Lord Ferry', cuerpo:'Desde hace muy buen tiempo oigo voces y escucho gritar a la gente. Mi cuerpo sólido y tieso a veces suele moverse muy rápido sobre un riel. Otras veces, se queda quieto, esperando que sea la hora de que el siguiente grupo de gente se suba sobre mí. Las manos que siento sobre mis barandas de metal los hacen sentir protegidos, listos para los siguientes minutos de diversión. Me alegro por ellos, porque tienen una libertad que no poseo. Ellos se van y yo me quedo esperando infinitamente el momento de terminar mi cansada jornada.'},
                    {titulo:'No hablo portugués muy bien', cuerpo:'Abro los ojos. Por alguna razón, siento que hace mucho tiempo no lo hago. Me encuentro echado en medio de un bello bosque abierto. Mi cabeza está sobre un tronco y me permite ver un cielo azul lleno de nubes blancas, como diciéndome, te esperan excelentes días. El sol no lo puedo ver, pero el día está en su máximo apogeo. La tierra es de un color amarillo intenso, el cual no ensucia, sino que se asemeja a un colchón suave que me sostiene.'}
                ]} />
                <Palabras />

            </div>
        </div>)
    }

}

export default Inicio;